.table {
  margin-bottom: 0;
  border-radius: 5px;

  th:first-child,
  td:first-child {
    padding-left: 1.5rem;
  }

  thead th {
    border: none;
    text-transform: uppercase;
    font-size: $h4-font-size;
    padding: 0.75rem;
    word-break : keep-all;
  }
  &.table-clickable tr {
    cursor: pointer;
  }
}

.table-bordered {
  th,
  td {
    border-left: none;
    border-right: none;
  }
}

.form-group-search  {
  position: relative;

  input {
    border-radius: $border-radius-lg*2;
    padding-left: $input-padding-x * 3;
  }

  &::before {
    font-family: 'material-icons';
    content: "\ec16";
    position: absolute;
    padding: $input-padding-y $input-padding-x;
    left: 0;
  }
}

.sticky-table-wrapper {
  overflow: auto;
  max-height: 450px;
  border-top: 1px solid $table-border-color;
}

.sticky-table {
  border-top: none !important;

  thead tr:nth-child(1) th {
    position: sticky;
    top: 0;
    z-index: 10;
  }
}