.loading-overlay {
  position: absolute;
  text-align: center;
  top: 0;
  left: 0;
  margin-left: 0;
  margin-top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  padding: 20px;
  align-items: center;
  justify-content: center;

  &::after {
    content: ' ';
    opacity: 0.5;
    background-color: #f4f4f9;
    width: 100%;
    height: 100%;
    position: absolute;

  }
}
