@font-face {
  font-family: 'material-icons';
  src:  url('material-icons.eot?a3l6fa');
  src:  url('material-icons.eot?a3l6fa#iefix') format('embedded-opentype'),
    url('material-icons.ttf?a3l6fa') format('truetype'),
    url('material-icons.woff?a3l6fa') format('woff'),
    url('material-icons.svg?a3l6fa#material-icons') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

.material-icons {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'material-icons' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

